import { Component, OnInit, ElementRef } from '@angular/core';
declare var jquery:any;
declare var $ :any;

import {GetdataService} from '../../services/getdata.service';
import {DeletedataService} from '../../services/deletedata.service';
import {AdddataService} from '../../services/adddata.service';

import {Http, Response, Headers} from '@angular/http';



import 'rxjs/add/operator/map';

import { FileUploader } from 'ng2-file-upload';

@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.css']
})
export class CategoriesComponent implements OnInit {
  category:Category[];
  website:Website[];
  title:string;
  deletedCatID:string;
  deletedCatCategory:string;
  editedCategory:string;
  months:string[];
  isavailable:boolean;
  categorydata:object;
  public submitted:false;


  constructor(private getdataService:GetdataService,  private deletedataService:DeletedataService, private adddataService:AdddataService, private http:Http, private elem: ElementRef) {
    setInterval(() => {
      this.getdataService.getCategory().subscribe((category) => {
        this.category=  category;
      },
    err=>{
      console.log(err);
      return false;
    });
    //get website
    this.getdataService.getWebsite().subscribe((website) => {
      this.website=  website;
      // console.log("dwkamk "+this.website);
    },
  err=>{
    console.log(err);
    return false;
  });
  }, 10000);

  }

  ngOnInit() {
    $(document).ready(function(){ $('ul.tabs').tabs(); });
    $(".footer-dwn").hide();
    $(window).scroll(function() {
       if($(window).scrollTop() + $(window).height() == $(document).height()) {
        $(".footer-dwn").show();
      }else{
          $(".footer-dwn").hide();
      }
    });

      ////////refresh category /////////////
      this.getdataService.getCategory().subscribe((category) => {
        this.category=  category;
        console.log(this.category);
      },
    err=>{
      console.log(err);
      return false;
    });
    this.getdataService.getWebsite().subscribe((website) => {
      this.website=  website;
      },
    err=>{
      console.log(err);
      return false;
    });
    //////////end refresh category.////////

  }




    deleteCategory = function(deletedCatID,deletedCatCategory) {
           if(confirm('Are you sure to delete category "'+deletedCatCategory+'"? All dependencies will be deleted.')) {
           this.submitted=true;
         this.deletedataService.deleteCategory(deletedCatID,deletedCatCategory).subscribe((res:Response)=>{
          console.log(res);
                this.submitted=false;
                 ////////refresh category /////////////
                 this.getdataService.getCategory().subscribe((category) => {
                   this.category=  category;
                 // console.log(  this.category);
                 },
               err=>{
                 console.log(err);
                 return false;
               });
               //////////end refresh category.////////
         });
      }
    }

    deleteWebsite = function(deletedWebsiteID) {

          this.submitted=true;
         this.deletedataService.deleteWebsite(deletedWebsiteID).subscribe((res:Response)=>{
          console.log(res);
                this.submitted=false;
                 ////////refresh website /////////////
                 this.getdataService.getWebsite().subscribe((website) => {
                   this.website=  website;
                   },
                 err=>{
                   console.log(err);
                   return false;
                 });
               //////////end refresh website.////////
         });

    }



      public addCategory(): void{

        let files = this.elem.nativeElement.querySelector('#image').files;
        let catnames = this.elem.nativeElement.querySelector('#name').value;
        let formData = new FormData();

        let file = files[0];
        //let catname = catnames[0];
        formData.append('image',file,file.name);
        formData.append('category',catnames);
      //  this.submitted=true;

        this.adddataService.addCategory(formData).subscribe
        (res =>{ alert("category "+catnames+" is now added.");

              ////////refresh category /////////////
              this.getdataService.getCategory().subscribe((category) => {
                this.category=  category;
                this.submitted=false;
              },
            err=>{
              console.log(err);
              return false;
            });
            //////////end refresh category.////////

      } );

      }

      public addWebsite(): void{

        let files = this.elem.nativeElement.querySelector('#site_image').files;
        let websiteurls = this.elem.nativeElement.querySelector('#website').value;
        let websitenames = this.elem.nativeElement.querySelector('#website_name').value;
        let formData = new FormData();

        let file = files[0];
        formData.append('image',file,file.name);
        // console.log("website link "+websiteurls);
        formData.append('website',websiteurls);
        formData.append('sitename',websitenames);
      //  this.submitted=true;

        this.adddataService.addWebsite(formData).subscribe
        (res =>{ alert("Website "+websiteurls+" is now added.");

              ////////refresh website ////////////
              this.getdataService.getWebsite().subscribe((website) => {
                this.website=  website;
              },
            err=>{
              console.log(err);
              return false;
            });
            //////////end refresh website.////////

      } );

      }



}

interface Category{
  _id:number,
  item:string,
  file_path:string,
  filename_path:string
}

interface Website{
  _id:number,
  Website:string,
  sitename:string,
  image:string,
  image_path:string
}



